@import url(https://fonts.googleapis.com/css?family=Work+Sans:300,400,500,600&display=swap);
/* FONTS */
@font-face {
    font-family: 'icomoon';
    src: url(/static/media/icomoon.9e96a58b.eot);
    src: url(/static/media/icomoon.9e96a58b.eot#iefix) format('embedded-opentype'), url(/static/media/icomoon.7e1cff6b.ttf) format('truetype'), url(/static/media/icomoon.60049a09.woff) format('woff'), url(/static/media/icomoon.9b345b5b.svg#icomoon) format('svg');
    font-weight: normal;
    font-style: normal;
}

html, body {
    margin: 0;
    padding: 0;
    background: #fcfcff;
}

.content {
    margin: auto;
    font-family: 'Work Sans', sans-serif;
    position: relative;
    z-index:99;
}

.white-box {
    background: #fff;
    padding: 20px 15px;
    border-radius: 5px;
}

.content.global {
    padding-top: 80px;
}

.content.big-header-enabled {
    padding-top: 150px;
}

.shadow {
    box-shadow: 0 3px 10px 0 #0000001f;
    background: #fff;
    border-radius: 5px;
}

@media only screen and (min-width: 1000px) {
    .content {
        width: 1000px;
    }
}

@media only screen and (max-width: 1050px) {
    .content {
        width: 95%;
    }
}

h1, h2, h3 {
    color: #025aa0;
}

@media only screen and (min-width: 1000px) {
    .inside-content {
        padding: 10px;
    }
    .content.search-panel form .radio-line {
        width: 50%;
    }
}

@media only screen and (max-width: 999px) {
    .inside-content {
        padding: 5px;
    }
}

.notice {
    text-align: center;
    color: #025aa0;
    font-weight: 300;
    font-size: 20px;
}

.popup-direction-form {
    position: fixed;
    background: #fff;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 999;
}

.loading {
    width: 50px;
    height: 50px;
    margin: 10px auto;
}

.loading img {
    width: 100%;
    height: 100%;
}

.wrong-field .ant-input {
    border: 1px solid #ff4650;
}

.error {
    text-align: center;
}

.error span {
    display: block;
    margin-top: 10px;
    color: #025aa0;
    font-family: 'Work Sans', sans-serif;
    font-weight: 400;
}

@media only screen and (min-width: 1000px) {
    .error img {
        width: 250px;
    }
    .error span {
        font-size: 25px;
    }
}

@media only screen and (max-width: 999px) {
    .error img {
        width: 60%;
    }
    .error span {
        font-size: 20px;
    }
}

footer {
    background: #1e2b33;
    padding: 10px;
    color: #fff;
    position: absolute;
    bottom:0;
    width: 100%;
    text-align: center;
    font-family: 'Work Sans', sans-serif;
    z-index:1;
}

footer img {
    width:10px;
    height:10px;
    margin-top:-2px;
}

footer a {
    color:#1890ff;
}

footer a:hover {
    text-decoration: underline;
}

@media only screen and (max-height: 620px) {
    footer {
        display: none;
    }
}
header {
    border-radius: 5px;
    margin-bottom: 20px;
    padding: 10px;
    overflow: hidden;
    position: fixed;
    width: 100%;
    z-index: 900;
}

header .top {
    overflow: hidden;
}

header .top .header-back-btn {
    float: left;
    margin-top: 5px;
    padding-right: 10px;
    margin-right: 10px;
    border-right: 1px solid #E2E8ED;
    cursor: pointer;
}

header .top .logo {
    float: left;
    overflow: hidden;
}

header .top .logo img {
    width: 40px;
    height: 40px;
    float: left;
    margin-right: 10px;
}

header .top .logo h1 {
    float: left;
    margin: 0;
    padding: 0;
    font-size: 20px;
    margin-top: 5px;
}

header .top .header-back-btn:hover .icon-arrow-left:before {
    color: #025aa0;
}

.icon-arrow-left:before {
    content: "\e901";
    font-family: 'icomoon';
    font-size: 20px;
    color: #bdc3c7;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
}

.big-header {
    -webkit-transition: all 2s ease;
    transition: all 2s ease;
}

header .details {
    margin-top: 15px;
    cursor: pointer;
}

header .details .line {
    overflow: hidden;
}

header .details .line.direction {
    margin-bottom:5px;
}

header .details .item {
    font-size:15px;
    color:#045AA0;
    float:left;
    margin-right:20px;
}

header .details .item:last-of-type {
    margin-right: 0;
}

header .details .item img {
    width:15px;
    height:15px;
    margin-right:10px;
}
/* Fade effect */

.fade-enter {
    opacity: 0;
}

.fade-enter-active {
    opacity: 1;
    -webkit-transition: all 300ms linear;
    transition: all 300ms linear;
}

.fade-exit {
    opacity: 1;
}

.fade-exit-active {
    opacity: 0;
    -webkit-transition: all 300ms linear;
    transition: all 300ms linear;
}

.fade-exit-done {
    opacity: 0;
}

.fade-appear {
    opacity: 0;
}

.fade-appear-active {
    opacity: 1;
    -webkit-transition: all 300ms linear;
    transition: all 300ms linear;
}

/* Slide effect */

.slide-arrow-header-enter {
    width: 0px;
    overflow: hidden;
    float: left;
}

.slide-arrow-header-enter-active {
    width: 40px;
    -webkit-transition: all 100ms linear;
    transition: all 100ms linear;
    overflow: hidden;
    float: left;
}

.slide-arrow-header-exit {
    width: 40px;
    overflow: hidden;
    float: left;
}

.slide-arrow-header-exit-active {
    width: 0px;
    -webkit-transition: all 100ms linear;
    transition: all 100ms linear;
    overflow: hidden;
    float: left;
}

.slide-arrow-header-exit-done {
    width: 0px;
    overflow: hidden;
    float: left;
}

.slide-arrow-header-appear {
    width: 0px;
    overflow: hidden;
    float: left;
}

.slide-arrow-header-appear-active {
    width: 100px;
    -webkit-transition: all 100ms linear;
    transition: all 100ms linear;
    overflow: hidden;
    float: left;
}
.content.search-panel h2:first-of-type {
    font-weight: 600;
    font-size: 30px;
}

.content.search-panel form span.title-form {
    font-size: 14px;
    color: #025aa0;
    font-weight: 500;
    display: block;
    margin-bottom: 5px;
    margin-top: 15px;
}

.content.search-panel form span.title-form:first-of-type {
    margin-top: 0;
}

.content.search-panel form button {
    margin-top: 30px;
    width: 100%;
}

.content.search-panel form .ant-radio-group-solid .ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled) {
    background: #57606f;
    border-color: #57606f;
}

.content.search-panel .time-form {
    display: flex;
}

@media only screen and (max-width: 999px) {
    .content.search-panel .time-form {
        justify-content: space-between;
    }
}

@media only screen and (min-width: 1000px) {
    .content.search-panel .time-form .item {
        margin-right: 50px;
    }
}

.content.search-panel .time-form .item {
    margin-top: 15px;
}

.content.search-panel form .radio-line {
    margin-top:20px;
}

.content.search-panel form .ant-radio-button-wrapper {
    width:50%;
    text-align: center;
}

.content.search-panel form .ant-radio-group {
    width:100%;
}
.content .btn-prev-next-group {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
}

.content .btn-prev-next-group .btn {
    color: #025aa0;
    font-size: 16px;
    cursor: pointer;
    margin: auto auto 10px auto;
    cursor: pointer;
}

.content .btn-prev-next-group .btn img {
    width: 22px;
    height: 22px;
    margin-top: -2px;
}

.content .btn-prev-next-group .btn.prev img {
    -webkit-transform: rotate(-90deg);
    transform: rotate(-90deg);
    margin-right: 5px;
}

.content .btn-prev-next-group .btn.next img {
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
    margin-right: 5px;
}

.content .title-day h2 {
    font-size: 30px;
}

.content .title-day h2 em {
    font-size: 15px;
    font-style: normal;
}

.direction-collection {
    width: 100%;
}

.content .how-works {
    margin-bottom: 20px;
}

.content .how-works .dots-item {
    padding:0!important;
}

.content .how-works span.description {
    display: block;
    margin:0;
    font-size:12px;
    color:#bdc3c7;
}

@media only screen and (max-width: 430px) {
    .content .how-works {
        margin-top:0px;
        margin-bottom:-15px;
    }
}

.content .how-works h2 {
    text-align: center;
    font-weight: 600;
    color: #484848;
    margin-bottom: 20px;
}

.content .how-works .dots {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

@media only screen and (max-width: 400px) {
    .content .how-works .dots .dots-item {
        width: 100%;
        margin-bottom: 20px;
    }
    .content .how-works .dots .dots-item:last-of-type {
        margin-bottom: 0;
    }
}

.content .how-works .ant-btn.ant-btn-primary {
    width: 100%;
    margin-top: 20px;
}

.how-works .dots-item span {
    font-size: 14px;
}

.how-works .close {
    width: 20px;
    height: 20px;
    position: relative;
    left: calc(100% - 20px);
    margin-bottom: -23px;
    padding: 0;
    margin-top: -2px;
    cursor: pointer;
}

.how-works .close:hover .icon-close:before {
    color: #025aa0;
}

.icon-close:before {
    content: "\e900";
    font-family: 'icomoon';
    font-size: 18px;
    margin: 0;
    color: #bdc3c7;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
}
.ant-timeline-item-head, .ant-timeline-item-tail {
    box-shadow: 0 3px 10px 0 #0000001f;
    border-radius: 5px;
}

.ant-timeline-item-head {
    margin-top: 5px;
}

.ant-timeline-item-head-red {
    background: #ff4757;
    border-color: #ff4757;
}

.ant-timeline-item-head-blue {
    background: #3742fa;
    border-color: #3742fa;
}

.ant-timeline-item-head-green {
    background: #2ed573;
    border-color: #2ed573;
}

.ant-timeline-item-head-grey {
    background: #a4b0be;
    border-color: #a4b0be!important;
}

.dots-item {
    overflow: hidden;
    padding-top: 30px;
    padding-bottom: 30px;
}

.dots-container {
    margin-left: calc((100% - 45px) / 2);
}

.dots-container .dot {
    width: 12px;
    height: 12px;
    border-radius: 100%;
    margin-right: 5px;
    float: left;
}

.dots-container .dot.green {
    background: #00da67;
}

.dots-container .dot.grey {
    background: #dfe8f0;
}

.dots-container .dot.red {
    background: #ff2b50;
}

.dots-container .dot.orange {
    background: #ffa100;
}

.dots-container .dot:last-of-type {
    margin-right: 0;
}

.dots-item span {
    display: block;
    margin-top: 20px;
    font-weight: 400;
    color: #484848;
    font-size: 12px;
    text-align: center;
}

.direction-collection .direction-item {
    margin-bottom: 20px;
    overflow: hidden;
    cursor: pointer;
}

.direction-collection .direction-item .ant-timeline-item {
    margin-left: 60px;
}

.direction-collection .direction-item .global-station-item {
    margin-left: -80px;
}

.direction-collection .direction-item .global-station-item .time {
    font-weight: 600;
    color: #484848;
    font-size: 18px;
    overflow: hidden;
}

.direction-collection .direction-item .global-station-item .time .time {
    float: left;
}

.direction-collection .direction-item .global-station-item .time .platform {
    float: left;
    margin-left: 30px;
    font-size: 14px;
    font-weight: 400;
}

.direction-collection .direction-item .global-station-item .informations {
    margin-left: 80px;
    margin-top: -28px;
    width: 100%;
}

.direction-collection .direction-item .timeline.extended .global-station-item .informations {
    width: auto!important;
}

.direction-collection .direction-item .global-station-item .informations h3 {
    margin: 0;
    padding: 0;
    color: #484848;
    font-weight: 300;
    font-size: 18px;
}

.direction-collection .direction-item .global-station-item .informations .direction {
    font-size: 15px;
    font-weight: 300;
    color: #484848;
    margin-top: 5px;
    font-style: italic;
}

.direction-collection .direction-item .global-dots {
    border-top: 1px dashed #e2e8ec;
    padding-top: 30px;
    margin-top: -10px;
    padding-bottom: 20px;
}

.direction-collection .direction-item .global-dots.extended {
    width: 100%;
    padding: 20px 0 0 0;
    border-top: 1px dashed #e2e8ec;
    margin-top: 10px;
    border-left: none;
}

.direction-collection .direction-item .global-dots.extended .dots-item {
    padding: 0;
}

.direction-collection .direction-item .timeline {
    width: calc(100% - 100px);
}

/* EXTENDED */

.direction-collection .direction-item .timeline.extended {
    width: 100%;
}

.direction-collection .direction-item .timeline.extended .global-station-item .informations {
    overflow: hidden;
}

.direction-collection .direction-item .timeline.extended .global-station-item .informations .station {
    float: left;
}

.station {
    width: calc(100% - 100px);
}

.direction-collection .direction-item .timeline.extended .global-station-item .informations .data-container {
    float: right;
    overflow: hidden;
}

.direction-collection .direction-item .timeline.extended .global-station-item .informations .data-container .graph-container, .direction-collection .direction-item .timeline.extended .global-station-item .informations .data-container .dots-item {
    float: left;
}

.detail .informations {
    padding-top: 18px;
}

.detail .ant-timeline-item-head {
    margin-top: 20px;
}

.ant-timeline-item-last>.ant-timeline-item-content {
    min-height: 0;
}

.ant-timeline-item-last {
    padding-bottom: 0;
}

.detail .global-station-item .time:last-of-type {
    margin-top: -140px;
    margin-bottom: 140px;
}

.notice-station {
    background: #fff;
    max-width: 400px;
    margin: 20px 10px;
    padding: 10px;
    border-radius: 3px;
    color: red;
}

.notice-station.empty {
    background: none;
    height:40px;
}

.graph-container {
    margin-right: 20px;
}

.direction-collection .direction-item .timeline.extended .global-station-item .informations .data-container {
    margin-top: -40px;
}

canvas {
    width: 200px !important;
    height: 200px !important;
}

@media only screen and (min-width: 450px) {
    .timeline {
        width: 100%;
        float: left;
    }
    .direction-collection .direction-item .global-dots {
        float: right;
        width: 90px;
        padding-left: 10px;
        border-top: 0;
        border-left: 1px solid #e2e8ec;
    }
}

@media only screen and (max-width: 449px) {
    .direction-collection .direction-item .global-dots {
        padding: 0;
        margin-top: 10px;
    }
    .direction-collection .direction-item .global-dots .dots-item {
        padding-top: 20px;
        padding-bottom: 0;
    }
    .station {
        width: 100%;
    }
}

@media only screen and (max-width: 520px) {
    .station, .data-container {
        float: none!important;
    }
    .data-container {
        margin-top: 10px;
    }
    .direction-collection .direction-item .timeline.extended .global-station-item .informations .data-container .dots-item {
        float: right!important;
    }

    .detail .informations {
        padding-top: 18px;
    }
    .detail .ant-timeline-item-head {
        margin-top: 20px;
    }
    
    .direction-collection .direction-item .timeline.extended .global-station-item .informations .data-container {
        margin-top: 10px;
    }

    .direction-collection .direction-item .timeline.extended .global-station-item .informations .station {
        margin-bottom: 20px;
        width:100%;
    }

    .notice-station {
        width:90%;
    }

    .content .btn-prev-next-group .btn {
        margin-top: 30px;
    }
}


.no-data {
    width: 200px;
    height: 200px;
    margin-right: 20px;
}

.no-data img {
    height: 200px;
    display: block;
    width: 200px;
    opacity: 0.5;
}

.no-data span {
    display: block;
    margin: -150px auto 0 auto;
    text-align: center;
    font-size: 20px;
    color: #484848;
}
